@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:left;font-family:'Quicksand', sans-serif}h1{font-weight:700}h2{font-weight:500}p{font-size:14px}small{display:block;margin-bottom:1rem;text-transform:uppercase;letter-spacing:1px;font-weight:600}.component{box-shadow:0px 0px 10px rgba(0,0,0,0.25);padding:1rem 1.5rem;margin-bottom:1.5rem}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#212121;display:flex;flex-direction:row;align-items:center;font-size:calc(6px + 2vmin);color:white;padding:1.25rem 3rem}.App-header h1{margin:0;margin-right:6.25rem}.App-header h2{margin:0}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}section{padding:3rem 20%;border-top:1px solid gray}small{color:gray}.layout{max-width:50rem;margin:0 auto;padding:5rem 1.25rem}.row{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between}.post{padding:1rem 1.5rem;border:1px solid #BDBDBD;margin:0.75rem 0;border-radius:0.25rem;position:relative}.post--active{border-color:#536DFE}.post h3 input,.post h3 textarea,.post p input,.post p textarea{font-size:inherit;line-height:inherit;border:none;width:100%;display:block;font:inherit;font-family:inherit;font-weight:inherit;padding:0;resize:none}.post h3 input:active,.post h3 input:focus,.post h3 textarea:active,.post h3 textarea:focus,.post p input:active,.post p input:focus,.post p textarea:active,.post p textarea:focus{outline:1px dashed rgba(83,109,254,0.5)}.post__remove{position:absolute;top:0;right:0;padding:0.5rem 0.875rem;line-height:1;font-size:1.5rem;background:transparent;border:none;outline:none;color:#F44336;cursor:pointer;transition:all 220ms ease-in-out}.post__remove:hover{transform:scale(1.25)}button{cursor:pointer}.btn{padding:1rem 2.5rem;color:#fff;font-weight:bold;background:#536DFE;border:none;outline:none;border-radius:0.25rem;font-size:0.875rem;letter-spacing:2px;margin:0.625rem 0;cursor:pointer;transition:all 220ms ease-in-out}.btn:hover{opacity:0.85}.btn--sort{background:transparent;border:1px solid #536DFE;color:#536DFE;margin-left:0.625rem}.btn--active{background:#009688;color:#fff}.loader{margin:auto;width:100%;display:flex;justify-content:center;align-items:center;min-height:12.5rem;letter-spacing:0.3125rem;font-size:1.875rem;font-weight:800;color:#536DFE}.notif{position:fixed;bottom:0.625rem;left:0.625rem;background:gray;color:#fff;padding:0.625rem 3rem}.sort{display:flex;flex-direction:column;align-items:flex-end}.search{display:flex;margin-bottom:1.5rem}.search__input{width:100%;font-size:1.125rem;margin:0;padding:1rem 1.125rem;font-size:0.875rem;border:1px solid #BDBDBD;border-radius:0.25rem}.search__input:focus,.search__input:active{border-color:#536DFE}

